/* icons */
$help-icons: ('pet-food', 'transportation', 'toys', 'bowls-and-cups',
  'shampoos', 'vitamins', 'medicines', 'collars-leashes',
  'sleeping-area');

$site-icons: ('credit-card', 'mail', 'phone', 'pin');

@each $icon in $help-icons {
  .icon-#{$icon} {
    background-image: url('../icons/icon-#{$icon}.svg');
  }
}

@each $icon in $site-icons {
  .icon-#{$icon} {
    background-image: url('../icons/#{$icon}.svg');
  }
}